.skills-wrapper {
  padding-top: 2em;
}

.skills-wrapper h2,
.skills-wrapper h3,
.skills-wrapper p {
  color: #2d3020;
  color: #fff;
}

.skills-wrapper .skills-header-row .skills-header-col {
  text-align: center;
  padding: 1.5em;
}

.skills-wrapper .skills-header-row p {
  text-align: center;
}

.skills-wrapper .skills-icons-row {
  padding: 1em;
}

.skills-wrapper .skills-icons-row .skills-icons-cols {
  display: grid;
  padding: 0;
  margin: 0;
  justify-items: center;
}

.skills-wrapper .skills-icons-row .skills-icons-cols .title-row {
  margin: 0;
  width: 100%;
}

.skills-wrapper .skills-icons-row .skills-icons-cols .title-row .title-col {
  text-align: center;
  display: grid;
  align-items: center;
  justify-items: center;
  text-align: center;
}

.skills-wrapper .skills-icons-row .skills-icons-cols .title-row .title-col p {
  border-bottom: 1px solid #f0d661;
  padding-bottom: 1px;
  width: 4em;
}

.skills-wrapper .skills-icons-row .skills-icons-cols .icons-row {
  padding: 0 3em;
  padding-top: 2em;
  margin-bottom: 2em;
}

.skills-wrapper .skills-icons-row .skills-icons-cols .icons-row .test-col {
  margin-top: 1em;
  display: grid;
  align-items: center;
  justify-items: center;
  text-align: center;
}

.skills-wrapper .skills-icons-row .skills-icons-cols .icons-row .test-col p {
  margin-top: 0.5em;
}

.skills-wrapper .skills-icons-row .skills-icons-cols .icons-row .chartjs-col {
  display: grid;
  justify-content: center;
  align-items: center;
  justify-items: center;
  text-align: center;
  margin-top: 1em;
}

.skills-wrapper .skills-icons-row .skills-icons-cols .icons-row .wp-icon {
  color: #fff;
}
