.john-wrapper {
  background-image: url("https://upload.wikimedia.org/wikipedia/commons/thumb/5/54/Claude_Monet%2C_Impression%2C_soleil_levant.jpg/771px-Claude_Monet%2C_Impression%2C_soleil_levant.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  padding: 0;
}

.john-wrapper .john-header-row .john-header-col {
  display: grid;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 2em;
  margin-top: 1em;
}

.john-wrapper .john-header-row .john-header-col h2 {
  font-size: 5rem;
  font-family: Arial, Helvetica, sans-serif;
  text-align: left;
  color: #191d1f;
  line-height: 4.5rem;
}

.john-wrapper .john-header-row .john-header-col h2 {
  text-decoration: underline;
}

.john-wrapper .john-header-row .john-header-col h5 {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 9px;
  text-decoration: none;
  line-height: 1rem;
}

.john-wrapper .john-header-row .john-header-col p {
  margin: 0;
  margin-top: 1em;
  font-size: 20px;
}

@media screen and (max-width: 768px) {
  .john-wrapper .john-header-row .john-header-col h2 {
    font-size: 2rem;
    line-height: 3rem;
  }
  .john-wrapper .john-header-row .john-header-col h5 {
    font-size: 7px;
  }
}

.john-wrapper .john-gif-row .john-gif-col {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5em 0;
  padding-top: 5em;
}

@media screen and (max-width: 768px) {
  .john-wrapper .john-gif-row .john-gif-col .john-div .john-gif-div .john-gif {
    width: 20em;
    height: 20em;
  }
}

.john-wrapper .sike-row .sike-col {
  display: grid;
  justify-content: center;
  text-align: center;
  padding: 2em;
  margin-bottom: 5rem;
}

.john-wrapper .sike-row .sike-col h3, .john-wrapper .sike-row .sike-col p {
  font-family: Arial, Helvetica, sans-serif;
  color: #F4F9FF;
}

.john-wrapper .sike-row .sike-col p {
  font-size: 20px;
}
