.post-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, 320px);
}

.post-wrapper .post {
  box-shadow: -1px 1px 8px -1px rgba(0, 0, 0, 0.75);
}

.post-wrapper .post .thumbnail {
  display: block;
  width: 326px;
  height: 286px;
  object-fit: cover;
}

.post-wrapper .post-preview {
  margin-top: 0.5em;
}

.post-wrapper .post-preview h6 {
  font-family: Arial, Helvetica, sans-serif;
  letter-spacing: -1px;
  font-size: 14px;
}

.post-wrapper .post-preview p {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 12px;
}
