button,
input {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  letter-spacing: 1.4px;
  color: black;
}

.form-wrapper {
  display: flex;
  justify-content: center;
  padding: 10px;
}

.form-wrapper .screen {
  margin: 2em 0;
  box-shadow: -1px 1px 3px -1px rgba(0, 0, 0, 0.75);
  flex: 0 1 700px;
  position: relative;
  background: #fff;
  border-radius: 15px;
}

.form-wrapper .screen .screen-header {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  background: #eaeaea;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  height: 2.5em;
}

.form-wrapper .screen .screen-header .screen-header-left {
  margin-right: auto;
}

.form-wrapper .screen .screen-header .screen-header-left .screen-header-button {
  display: inline-block;
  width: 8px;
  height: 8px;
  margin-right: 3px;
  border-radius: 8px;
  background: white;
}

.form-wrapper .screen .screen-header .screen-header-left .screen-header-button.close {
  background: #ed1c6f;
}

.form-wrapper .screen .screen-header .screen-header-left .screen-header-button.maximize {
  background: #e8e925;
}

.form-wrapper .screen .screen-header .screen-header-left .screen-header-button.minimize {
  background: #74c54f;
}

.form-wrapper .screen .screen-header .screen-header-right {
  display: flex;
}

.form-wrapper .screen .screen-header .screen-header-right .screen-header-ellipsis {
  width: 3px;
  height: 3px;
  margin-left: 2px;
  border-radius: 8px;
  background: #222222;
}

.form-wrapper .screen:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 20px;
  right: 20px;
  bottom: 0;
  border-radius: 15px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
  z-index: -1;
}

.screen-body {
  display: flex;
}

.screen-body .screen-body-item {
  flex: 1;
  padding: 50px;
}

.screen-body .screen-body-item .app-title {
  display: flex;
  flex-direction: column;
  position: relative;
  color: #f7d850;
  font-size: 2rem;
}

.screen-body .screen-body-item .app-title H3 {
  color: #fff;
  border: 1px solid #222222;
  background-color: #222222;
  width: fit-content;
}

.screen-body .screen-body-item .app-title:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  bottom: -10px;
  width: 25px;
  height: 4px;
  background: #666;
}

.screen-body .screen-body-item .app-contact {
  margin-top: auto;
  font-size: 8px;
  color: #666;
}

.screen-body .screen-body-item.left {
  display: flex;
  flex-direction: column;
}

.screen-body .screen-body-item .app-form .app-form-group {
  margin-bottom: 15px;
}

.screen-body .screen-body-item .app-form .app-form-group .app-form-control {
  width: 100%;
  padding: 10px 0;
  background: none;
  border: none;
  border-bottom: 1px solid #666;
  color: #222222;
  font-size: 14px;
  outline: none;
  transition: border-color 0.2s;
}

.screen-body .screen-body-item .app-form .app-form-group .app-form-control::placeholder {
  color: #666;
}

.screen-body .screen-body-item .app-form .app-form-group .app-form-control:focus {
  border-bottom-color: #ddd;
}

.screen-body .screen-body-item .app-form .app-form-group.message {
  margin-top: 40px;
}

.screen-body .screen-body-item .app-form .app-form-group.buttons {
  margin-bottom: 0;
  text-align: right;
}

.screen-body .screen-body-item .app-form .app-form-group.buttons .app-form-button {
  background: none;
  border: none;
  color: #666;
  font-size: 14px;
  cursor: pointer;
  outline: none;
}

.screen-body .screen-body-item .app-form .app-form-group.buttons .app-form-button:hover {
  color: #fff;
  border: 1px solid #222222;
  background-color: #222222;
}

@media screen and (max-width: 520px) {
  .screen-body {
    flex-direction: column;
  }
  .screen-body-item.left {
    margin-bottom: 30px;
  }
  .app-title {
    flex-direction: row;
  }
  .app-title span {
    margin-right: 12px;
  }
  .app-title:after {
    display: none;
  }
}

@media screen and (max-width: 600px) {
  .screen-body {
    padding: 40px;
  }
  .screen-body-item {
    padding: 0;
  }
}
