.about-wrapper {
  padding: 0;
  min-height: 30em;
  padding-bottom: 2em;
}

.about-wrapper .about-me-col {
  padding: 2em;
}

.about-wrapper .about-me-col .about-me {
  display: grid;
  justify-items: center;
  padding: 1em;
}

.about-wrapper .about-me-col .about-me .bio {
  text-align: left;
}

.about-wrapper .about-me-col .about-me .bio .bio-paragraph-1 {
  color: #191D1F;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
}

.about-wrapper .about-me-col .about-me .bio .bio-paragraph-2 {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  color: #191d1f;
}

.about-wrapper .about-me-col .about-me .bio .signature {
  font-family: "Indie Flower", cursive;
  font-weight: bold;
  font-size: 18px;
}

.about-wrapper .about-me-col .about-me .bio .my-links a {
  margin-right: 0.4em;
  color: inherit;
}

@media screen and (max-width: 768px) {
  .about-wrapper .about-me-col {
    padding: 1em;
  }
}

.about-wrapper .playlist {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding-top: 2em;
}

.about-wrapper .playlist .synth {
  height: 100px;
  width: 100px;
  justify-self: center;
}

@media screen and (max-width: 768px) {
  .about-wrapper .playlist {
    padding-top: 1em;
  }
}
