.portfolio-wrapper {
  padding: 0;
}

.portfolio-wrapper .portfolio-header-row .portfolio-header-col {
  display: grid;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding-top: 2em;
  margin-top: 1em;
}

.portfolio-wrapper .portfolio-header-row .portfolio-header-col h2 {
  font-size: 5rem;
  text-align: left;
  color: #191d1f;
  line-height: 4.5rem;
}

.portfolio-wrapper .portfolio-header-row .portfolio-header-col h2 {
  text-decoration: underline;
  font-family: Arial, Helvetica, sans-serif;
}

.portfolio-wrapper .portfolio-header-row .portfolio-header-col h5 {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 10px;
  text-decoration: none;
  line-height: 1rem;
}

.portfolio-wrapper .portfolio-header-row .portfolio-header-col p {
  margin: 0;
  margin-top: 1em;
  font-size: 20px;
  letter-spacing: -1px;
  font-family: Arial, Helvetica, sans-serif;
}

@media screen and (max-width: 768px) {
  .portfolio-wrapper .portfolio-header-row .portfolio-header-col h2 {
    font-size: 2rem;
    line-height: 3rem;
  }
  .portfolio-wrapper .portfolio-header-row .portfolio-header-col h5 {
    font-size: 7px;
  }
}

.portfolio-wrapper .portfolio-header-row .curadoria-col {
  display: grid;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 0;
  padding: 2em;
}

.portfolio-wrapper .portfolio-header-row .curadoria-col p {
  font-size: 20px;
  letter-spacing: -1px;
  font-family: Arial, Helvetica, sans-serif;
}

.portfolio-wrapper .portfolio-cards-row .portfolio-cards-col {
  display: grid;
  justify-content: center;
  padding: 2em;
}
